@import url('https://fonts.googleapis.com/css?family=Roboto:400,500');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {

  font-family: 'Roboto', sans-serif;
}

.wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  padding: 0 20px;
  background: #34495e;
}

.container {
  max-width: 1000px;
  width: 100%;
}

.paper {
  min-height: 200px;
  width: 100%;
  padding: 1.5rem 2.5rem;
  border-radius: 2px;

  background: repeating-linear-gradient(0deg,
      #ecf0f1,
      #ccc 1px,
      #ecf0f1 1px,
      #ecf0f1 30px);
  background-position: top left;
}

.paragraph {

  line-height: 30px;
  font-weight: 400;
}

.nav {
  position: fixed;
  top: 0;
  width: 100%;
  height: 60px;
  display: flex;
  justify-content: space-between;

  background: lighten(#34495e, 5%);
}

.nav__button {
  display: inline-block;
  height: 100%;
  padding: 10px 20px;

  background: none;
  border: none;
  color: #fff;
  font-weight: 500;
  font-size: 1rem;

  transition: 250ms;
  cursor: pointer;

  &:hover {
    background: lighten(#34495e, 10%);
  }
}